	.fancy-active {

		.fancybox {

			&-overlay {}

			&-wrap {
				top: 0 !important;
				left: 0 !important;
				width: 100% !important;
				height: 100vh !important; }

			&-inner {
				width: 100% !important;
				min-height: 100% !important;
				overflow-y: scroll !important; }

			&-skin, &-outer {
				height: 100% !important; }

			&-type-image {

				.fancybox-inner {
					display: flex !important;
					justify-content: center; } } } }

	.forms {
		padding: 18px;
		min-height: 100%; }

	.fancybox {

		&-close {
			left: initial;
			right: 0; }

		&-overlay {

			&-fixed {
				// bottom: auto !important
				// right: auto !important
				// width: 100vw !important
 } }				// height: 100vh !important

		&-wrap {
			// width: 100vw !important
			// height: 100vh !important
			// top: 0 !important
			// left: 0 !important
			// right: auto !important
			// bottom: auto !important
 }			// overflow: hidden !important

		&-inner {
 }			// padding: 20px 0

		&-skin {
			// width: 100% !important
			// height: 100% !important

			div {
				// width: 100% !important
 }				// height: 100% !important

			.fancybox-image {
				// width: 100% !important
				// //margin: 20px 0
 } } }				// height: calc(100% - 40px) !important

	.forms {
		margin: 0 !important;

		&-input-cont {
			width: 100%; }

		&__label {

			&--file {

				&:before {
					// background-image: none
					text-align: center;
					padding-right: 0;
					text-indent: -9999px;
					overflow: hidden;
					background-position: center;
					width: 40px; } } }

		&-wrap {

			&--bot {

				> div {
					width: 100%;

					&:not(:first-child) {
						margin-top: vh(15); } } } }


		&-checkbox {}

		&__checkbox {
			width: 100%; }

		&__req-text {
			text-align: center; } } }
